/* Contenedor Principal */
.admin-container {
  display: flex;
  height: 100vh;
  background-color: #f9f9f9;
}

/* Barra Lateral */
.admin-menu {
  width: 250px;
  background-color: #2c3e50;
  color: #ecf0f1;
  padding: 20px 0;
}

.menu-title {
  text-align: center;
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 20px;
}

.menu-list {
  list-style: none;
  padding: 0;
}

.menu-list li {
  padding: 10px 20px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.menu-list li:hover {
  background-color: #34495e;
}

.menu-list li.active {
  background-color: #2980b9;
  color: white;
  font-weight: bold;
}

/* Contenido Principal */
.admin-main {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.admin-header {
  background-color: #f4f4f4;
  color: #333;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #ddd;
}

.btn-logout {
  background-color: #e53935;
  color: white;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.3s ease;
}

.btn-logout:hover {
  background-color: #c62828;
}

/* Dashboard Inicial */
.dashboard {
  text-align: center;
  padding: 20px;
  color: #555;
  font-size: 16px;
}

.content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f9f9f9;
}