.vista-resena {
    padding: 20px;
  }
  
  .contenedor-negocios,
  .contenedor-reseñas {
    margin-bottom: 20px;
  }
  
  .lista-negocios {
    list-style-type: none;
    padding: 0;
  }
  
  .lista-negocios li {
    padding: 10px;
    cursor: pointer;
    background-color: #f4f4f4;
    border: 1px solid #ccc;
    margin-bottom: 5px;
  }
  
  .lista-negocios li.negocio-activo {
    background-color: #d9f7be;
    border-color: #52c41a;
    font-weight: bold;
  }
  
  .tabla-reseñas {
    width: 100%;
    border-collapse: collapse;
  }
  
  .tabla-reseñas th,
  .tabla-reseñas td {
    padding: 10px;
    border: 1px solid #ccc;
    text-align: left;
  }
  
  .tabla-reseñas th {
    background-color: #f4f4f4;
  }
  
  .tabla-reseñas button {
    padding: 5px 10px;
    background-color: #ff4d4f;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  .tabla-reseñas button:hover {
    background-color: #d9363e;
  }