/* Contenedor principal */
.vista-negocio-container {
    padding: 20px;
    background-color: #f9f9f9;
  }
  
  .vista-negocio-container h2 {
    text-align: center;
    color: #333;
    margin-bottom: 20px;
  }
  
  /* Tabla */
  .negocios-table {
    width: 100%;
    border-collapse: collapse;
    margin: 0 auto;
    background-color: white;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .negocios-table th,
  .negocios-table td {
    border: 1px solid #ddd;
    padding: 10px;
    text-align: center;
  }
  
  .negocios-table th {
    background-color: #34495e;
    color: white;
  }
  
  .negocios-table tr:nth-child(even) {
    background-color: #f4f4f4;
  }
  
  .negocios-table tr:hover {
    background-color: #f1f1f1;
  }
  
  /* Imagen */
  .negocio-imagen {
    width: 100px;
    height: auto;
    border-radius: 4px;
  }
  
  /* Estado */
  .estado-validado {
    color: green;
    font-weight: bold;
  }
  
  .estado-pendiente {
    color: red;
    font-weight: bold;
  }
  
  /* Botón de Validar */
  .btn-validar {
    background-color: #3498db;
    color: white;
    border: none;
    padding: 5px 10px;
    cursor: pointer;
    border-radius: 4px;
    transition: background-color 0.3s ease;
  }
  
  .btn-validar:hover {
    background-color: #2980b9;
  }